import React, { Component } from 'react';
import logo from '../pic/logo.svg';




class Home extends Component {
  render() {
    return (
      <div className="Home content">

<img src = {logo} className= "App-logo" alt= "react logo"/>


        
      </div>
    );
  }
}

export default Home; 


